*{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
}

body{
  padding: 60px;
  background-color: rgb(25, 25, 37);
  color: #ffff;
}

input{
  width: 100%;
  font-size: 1.2rem;
  color: grey;
  margin: 20px 0;
  outline: none;
  border-radius: 30px;
  padding: 10px;
  border: none;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.card{
  border: 1px solid grey;
  margin: 30px 20px;
  color: #ffff;
}

.card p{
  padding: 10px;
}

.card img{
  max-height: 300px;
  width: 100%;
}